h1 {
    color: #00AFC7;
}

h2 {
    color: #00AFC7;
}

.card_container {
  display: flex;
  flex-wrap: wrap;
}

.character_card {
  width: 310px;
  height: 545px;
  background-color: rgb(108, 214, 21);
  border-radius: 5px;
  padding: 2px 16px;
  margin: 10px;
  cursor: pointer;
}

.location_card {
  width: 350px;
  height: 220px;
  background-color: rgb(108, 214, 21);
  border-radius: 5px;
  padding: 2px 16px;
  margin: 10px;
  cursor: pointer;
}

.episode_card {
  width: 350px;
  height: 200px;
  background-color: rgb(108, 214, 21);
  border-radius: 5px;
  padding: 2px 16px;
  margin: 10px;
  cursor: pointer;
}

.character_card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.location_card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.episode_card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}